@import './variables.scss';

@mixin onDesktop {
  @media (min-width:$desktop-min-width) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width:$tablet-min-width) {
    @content;
  }
}


@mixin consentPaddingInline() {
  padding-inline: 30px;

  @include onTablet {

  }

  @include onDesktop() {

  }
}

